
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}

input {
  width: 5em;
}
input[type="number"] {
  width: 3em;
}
ol.items > li, .controls {
  margin-top: 1rem;
}

table.confirmation {
  text-align: left;
  width: 100%;
}
table.confirmation .total {
  text-align: right;
}
tr:nth-child(even) {
  background-color: #f2f2f2;
}


label {
  white-space:nowrap;
}
.item option {
  width: 5rem;
}
label {
  font-size: 0.5rem;
}

.modified {
  background-color: #FEE;
}

img.design {
  vertical-align: bottom;
  height: 1.12em;
  width: 1.12em;
}
